import React from 'react';
import { Link } from 'react-router-dom';

const NavSoluciones = props =>
{
    return (
        <div>
            <Link to={props.to} className="flex items-start p-3 -m-3 rounded-lg hover:bg-gray-50">
                <svg xmlns="http://www.w3.org/2000/svg" className="flex-shrink-0 w-6 h-6 text-crismon-50" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d={props.d}/>
                </svg>
                <div className="ml-4">
                    <p className="text-base font-medium text-gray-900">
                        {props.title}
                    </p>
                    <p className="mt-1 text-sm text-gray-500">
                        {props.content}
                    </p>
                </div>
                </Link>
        </div>
    );
}

export default NavSoluciones;

