import React from 'react'
import { Link } from 'react-router-dom';

import { CloudIcon } from '@heroicons/react/outline'


export const CardPricing = (props) => {
    return (
        <div>
            <div className={`flex flex-col items-center justify-center min-h-full py-8 px-6 border-l border-r border-gray-200 + ${props.white}`}>
                <div className={`px-3 py-1 mb-6 rounded-full text-xxs  text-crismon-100 ${props.bgSell}`}>{props.sell}</div>
                <div className="mb-6">
                    <CloudIcon className={`h-14 w-14 ${props.bgCloud}`}/>
                </div>
                <p className="text-base font-bold text-center text-gray-600">
                            $ <span className="px-2 text-4xl font-bold text-maroon-700">{props.price}</span> MXN<br />
                            <span className="font-medium">/mensual</span>
                        </p>
                <p className="mb-3 text-2xl font-bold text-center text-gray-800">{props.type}</p>
                <p className="w-full mb-6 text-sm font-normal text-center text-gray-600">{props.legend}</p>
                <Link to={props.to} className={props.class}>Contratar</Link>
            </div>
        </div>
    )
}

export default CardPricing;
