import React, { useState } from "react";
import { Link } from "react-router-dom";
import SolucionesButton from '../../Components/Buttons/SolucionesButton'
import Toggle from "../../Components/DarkModeToggle/DarkModeToggle";
import SocialMedia from "../../Components/SocialMedia/SocialMedia";

import { HomeIcon } from '@heroicons/react/outline'
import { SpeakerphoneIcon} from '@heroicons/react/outline'
import { ServerIcon } from '@heroicons/react/outline'
import { WifiIcon } from '@heroicons/react/outline'
import { CloudIcon } from '@heroicons/react/outline'
import { VideoCameraIcon } from '@heroicons/react/outline'
import { PhoneIcon } from '@heroicons/react/outline'
import { DesktopComputerIcon } from '@heroicons/react/outline'

import LogoSchnelke from '../../Files/LogoSchnelke.svg'

export default function Navbar() {
    
    const [show, setShow] = useState(null);
    return (
            <div className="fixed z-50 min-w-full">
                
                {/*Contact Bar */}
                <div className="hidden xl:block">
                    <div className="flex flex-col justify-between px-6 py-2 bg-gray-50 sm:flex-row">
                        <a  href="tel:44444930404" className="flex items-center text-xs font-medium text-gray-600 transition duration-150 ease-in hover:text-crismon-100">
                            <PhoneIcon className="w-4 h-4 mr-2"/>
                            +52 444 493 0404
                        </a>
                        <SocialMedia />
                    </div>
                </div>


                {/*Computers */}
                <nav className="hidden min-w-full bg-white shadow xl:block">
                    <div className="container px-6 py-2 mx-auto xl:py-0">
                        <div className="flex items-center justify-start">
                            
                            <div className="flex items-center w-full sm:w-auto sm:items-stretch sm:justify-start">
                                <Link to="/" className="flex items-center ">
                                    <img className="w-12 h-12" src={LogoSchnelke} alt="Schnelke Soluciones" />
                                    <p  className="hidden pl-3 font-sans text-2xl font-bold leading-normal text-gray-700 sm:block">Schnelke</p>
                                </Link>
                            </div>
                            <div className="px-8">
                                <div className="hidden xl:flex md:mr-6 xl:mr-16">
                                    <Link to="/" className="flex items-center p-5 text-sm leading-5 text-gray-700 transition duration-150 ease-in-out hover:bg-gray-100 focus:bg-gray-100 focus:outline-none">
                                        Inicio
                                    </Link>
                                    <SolucionesButton />
                                </div>
                            </div>
                            <div className="items-center justify-end hidden md:flex md:flex-1 lg:w-0">
                                <div className="relative mx-4">
                                    <Toggle />
                                </div>
                                <Link to="/contacto" className="border btn text-maroon-600 border-maroon-600 hover:border-crismon-100 hover:text-crismon-100 hover:bg-red-100 focus:bg-crismon-50 focus:text-white">
                                    Cont√°ctanos
                                </Link>                       
                            </div>
                        </div>
                    </div>
                </nav>
                
                {/*Tables and Mobile (Responsive) */}
                <nav>
                    <div className="fixed flex items-center justify-between w-full px-6 py-2 bg-white xl:hidden">
                        <Link to="/" >
                            <img className="w-12 h-10" src={LogoSchnelke} alt="Schnelke Soluciones" />
                        </Link>
                        <div className="flex items-center">
                            <div id="menu" className="text-gray-800" onClick={() => setShow(!show)}>
                                {show ? (
                                    ""
                                ) : (
                                    <svg xmlns="http://www.w3.org/2000/svg" className="icon icon-tabler icon-tabler-menu-2" width={24} height={24} viewBox="0 0 24 24" strokeWidth="1.5" stroke="currentColor" fill="none" strokeLinecap="round" strokeLinejoin="round">
                                        <path stroke="none" d="M0 0h24v24H0z" fill="none" />
                                        <line x1={4} y1={6} x2={20} y2={6} />
                                        <line x1={4} y1={12} x2={20} y2={12} />
                                        <line x1={4} y1={18} x2={20} y2={18} />
                                    </svg>
                                )}
                            </div>
                        </div>
                    </div>
                    
                    {/*Mobile responsive sidebar*/}
                    <div className={show ? "w-full xl:hidden top-0 h-screen absolute z-40 transform  translate-x-0  ease-in-out duration-300" : "   w-full xl:hidden h-full absolute z-40  transform -translate-x-full ease-in-out duration-200"}>
                        <div className={show ? "w-full h-screen bg-gray-800 opacity-50 ease-in-out duration-300 transition delay-200 " : "bg-transparent ease-in-out"} onClick={() => setShow(!show)} />
                        <div className="fixed top-0 z-40 flex-col justify-between w-64 h-full pb-4 overflow-y-auto transition duration-150 ease-in-out bg-white divide-y-2 divide-gray-600 shadow-lg ring-1 ring-black ring-opacity-5 xl:hidden">
                            <div className="h-full px-5">
                                <div className="flex justify-end pt-4">
                                    <div id="cross" className="text-gray-800" onClick={() => setShow(!show)}>
                                        <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" strokeWidth="1.5" stroke="currentColor" fill="none" strokeLinecap="round" strokeLinejoin="round">
                                            <path stroke="none" d="M0 0h24v24H0z" />
                                            <line x1={18} y1={6} x2={6} y2={18} />
                                            <line x1={6} y1={6} x2={18} y2={18} />
                                        </svg>            
                                    </div>
                                </div>
                                <div className="flex flex-col justify-between w-full">
                                    <div>
                                        <div className="w-full">
                                            <div className="flex items-center justify-start w-full mt-4">
                                                <div className="flex items-center">
                                                    <Link to="/" onClick={() => setShow(!show)}>
                                                        <img className="inline-block w-11 h-11" src={LogoSchnelke} alt="Schnelke Soluciones" />
                                                        <p className="inline-block ml-2 font-sans text-2xl font-bold text-gray-800">Schnelke</p>
                                                    </Link>
                                                </div>

                                                <div className="flex items-center justify-end flex-1">
                                                    <div className="relative -mt-1">
                                                        <Toggle />
                                                    </div>
                                                </div>
                                            </div>
                                            
                                        </div>
                                        <SocialMedia/>
                                        <ul className="mt-5">
                                            <Link to="/" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center ">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <HomeIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">Inicio</p>
                                                    </div>
                                                </li>
                                            </Link>
                                            <Link to="/pbx" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <SpeakerphoneIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">PBX</p>
                                                    </div>
                                                </li>
                                            </Link>
                                            <Link to="/hosting" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <ServerIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">Hosting</p>
                                                    </div>
                                                </li>
                                            </Link>
                                            <Link to="/troncal-sip" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <WifiIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">Troncal SIP</p>
                                                    </div>
                                                </li>
                                            </Link>
                                            <Link to="/vps" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <CloudIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">VPS</p>
                                                    </div>
                                                </li>
                                            </Link>
                                            <Link to="/video-vigilancia" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <VideoCameraIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">Videovigilancia</p>
                                                    </div>
                                                </li>
                                            </Link>
                                            <Link to="/contact-center" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <PhoneIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">Contact Center</p>
                                                    </div>
                                                </li>
                                            </Link>
                                            <Link to="/diseno-web" className="cursor-pointer" onClick={() => setShow(!show)}>
                                                <li className="btn-menu">
                                                    <div className="flex items-center">
                                                        <div className="w-6 h-6 text-maroon-700">
                                                            <DesktopComputerIcon />
                                                        </div>
                                                        <p className="ml-3 text-base text-gray-800 xl:text-base">Dise√±o Web</p>
                                                    </div>
                                                </li>
                                            </Link>
                                        </ul>
                                    </div>
                                    <div className="w-full py-6">
                                        <div className="border-t border-gray-300">
                                            <div className="w-full pt-4">
                                                <p className="mb-4 text-base font-medium text-center text-gray-500">
                                                    ¬øTienes alguna duda?
                                                </p>
                                                <Link
                                                    onClick={() => setShow(!show)}
                                                    to="/contacto"
                                                    className="flex justify-center text-white btn bg-maroon-700 hover:bg-crismon-500"
                                                >
                                                    Cont√°ctanos
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
    );
}
