import React from 'react';
import { ThemeContext } from '../../themeContext';
import { MoonIcon } from '@heroicons/react/solid'
import { SunIcon } from '@heroicons/react/solid'

const DarkModeToggle = () => {
    const { theme, setTheme } = React.useContext(ThemeContext);

    return (
        <div className="transition duration-500 ease-in-out rounded-full xl:p-2">
            {theme === 'dark' ? (
                <SunIcon onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')} className="w-8 h-8 p-1 text-gray-600 transition duration-150 ease-in-out bg-gray-200 border-gray-300 rounded-full cursor-pointer xl:rounded focus:outline-none hover:bg-gray-300" />
            ) : (
                <MoonIcon  onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')} className="w-8 h-8 p-1 text-gray-600 transition duration-150 ease-in-out bg-gray-200 border-gray-300 rounded-full cursor-pointer xl:rounded focus:outline-none hover:bg-gray-300" />
                )}
        </div>
    );
};

export default DarkModeToggle;

