import React from 'react'
import { Link } from 'react-scroll';
export const HeaderSolutions = (props) => {
    return (
        <header>
            <div className="w-full bg-center bg-cover h-128" style={{backgroundImage : `url(${props.img})`}} >
                <div className="flex items-end justify-center w-full h-full bg-opacity-75 bg-dark-900 md:items-center">
                    <div className="py-8 text-center">
                        <h1 className="px-4 mb-6 text-2xl font-semibold text-white uppercase lg:text-3xl">{props.title} <span className="text-crismon-200">{props.subtitle}</span></h1>
                        <Link to="pricing" smooth={true} duration={1000} className="font-medium text-white uppercase btn bg-maroon-700 hover:bg-maroon-800 lg:hidden">{props.button}</Link>
                    </div>
                </div>
            </div>
        </header>
        
    )
}


export default HeaderSolutions;
